<template>
  <v-container bg fill-height>
    <v-card elevation="2" class="mx-auto my-12" max-width="700" width="700">
      <v-card-title>Time Stamped Records</v-card-title>
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            ref="product"
            v-model="product"
            type="number"
            :counter="11"
            :rules="[v => v.length === 11 || 'Number must be 11 characters']"
            label="Product"
            required
            @keyup="focusNext"
          />

          <v-text-field
            v-model="status"
            type="number"
            :counter="5"
            :rules="[v => v.length === 5 || 'Number must be 5 characters']"
            label="Inspection Status"
            required
            @keyup="focusNext"
          />

          <v-text-field
            v-model="user"
            type="number"
            :counter="6"
            :rules="[v => v.length === 6 || 'Number must be 6 characters']"
            label="Manufacture User Designation"
            required
            @keyup="focusNext"
          />

          <v-text-field
            v-model="qc_user"
            type="number"
            :counter="6"
            :rules="[v => v.length === 6 || 'Number must be 6 characters']"
            label="QC User Designation"
            required
            @keyup="focusNext"
          />

          <v-btn ref="submit" block color="success" class="mr-4 mt-10" @click="validate">
            Submit
          </v-btn>
          <v-btn ref="submit" block color="error" class="mr-4 mt-10" @click="resetForm">
            Reset Form
          </v-btn>
        </v-form>
      </v-card-text>
    </v-card>
    <ErrorPopUp :last-error="last_error" :error-dialog="error_dialog" />
    <div class="text-center">
      <v-snackbar v-model="success_snack" :timeout="timeout" color="success">
        Succefully Posted!
        <template v-slot:action="{ attrs }">
          <v-btn color="red" text v-bind="attrs" @click="success_snack = false">
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-container>
</template>

<script>
import axios from "axios";
import ErrorPopUp from "@/components/errors/ErrorPopUp.vue";

export default {
  name: "Home",
  components: {
    ErrorPopUp: ErrorPopUp
  },
  data: () => ({
    tab: null,
    valid: false,
    product: "",
    status: "",
    user: "",
    qc_user: "",
    timeout: "2000",
    success_snack: false,
    last_error: "",
    error_dialog: false
  }),
  mounted() {
    this.focusProduct();
  },
  methods: {
    focusProduct() {
      this.$refs.product.focus();
    },
    focusNext(e) {
      if (e.key === "Enter") {
        const inputs = Array.from(e.target.form.querySelectorAll("input[type='number']"));
        const index = inputs.indexOf(e.target);

        if (index < inputs.length && index !== 3) {
          inputs[index + 1].focus();
        }
      }
    },
    sendToInflux(product, status, user, QCuser) {
      const body = {
        product,
        status,
        user,
        QCuser
      };
      axios
        .post("/api/qc", body)
        .then(() => {
          this.success_snack = true;
          this.resetForm();
        })
        .catch(error => {
          this.last_error = error;
          this.error_dialog = true;
        });
    },
    validate() {
      const validated = this.$refs.form.validate();
      if (validated) {
        this.sendToInflux(this.product, this.status, this.user, this.qc_user);
      }
    },
    resetForm() {
      this.$refs.form.resetValidation();
      this.product = "";
      this.status = "";
      this.user = "";
      this.qc_user = "";
      this.focusProduct();
    }
  }
};
</script>
